import { useEffect, useState } from "react";
import AdminNavbar from "../components/AdminNavbar";
import API from "../utils/api";
import "../styles/admin.css";

export default function AdminDashboard() {
  const [stats, setStats] = useState({});
  const [stores, setStores] = useState([]);
  const [sort, setSort] = useState("name:asc");
  const [search, setSearch] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  /* ================= FETCH ================= */
  useEffect(() => {
    const timer = setTimeout(() => {
      fetchDashboard();
    }, 400);

    return () => clearTimeout(timer);
  }, [sort, search]);

  const fetchDashboard = async () => {
    setLoading(true);
    setError(null);

    try {
      const [statRes, storeRes] = await Promise.all([
        API.get("/admin/dashboard"),
        API.get(`/admin/stores?sort=${sort}&q=${encodeURIComponent(search)}`)
      ]);

      //  FIX 1: correct structure
      setStats(statRes.data?.data || {});

      //  FIX 2: normalize stores
      const storeList = storeRes.data?.data || [];

      setStores(storeList);

    } catch (err) {
      console.error("Dashboard Error:", err);
      setError(" Failed to load dashboard");
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      <AdminNavbar />
      <div className="top-bar">
      <h1 className="admin-title">Admin Dashboard</h1>

      <button className="refresh-btn" onClick={fetchDashboard}>
      Refresh
     </button>
     </div>
      <div className="admin-container">
        <h1 className="admin-title">Admin Dashboard</h1>

        {error && <div className="error-message">{error}</div>}

        {loading ? (
          <div className="loading"> Loading...</div>
        ) : (
          <>
            {/* ================= STATS ================= */}
            <div className="stats-grid">
              <div className="stat-card">
                <h3>Total Users</h3>
                <p>{stats.totalUsers ?? 0}</p>
              </div>

              <div className="stat-card">
                <h3>Total Stores</h3>
                <p>{stats.totalStores ?? 0}</p>
              </div>

              <div className="stat-card">
                <h3>Total Ratings</h3>
                <p>{stats.totalRatings ?? 0}</p>
              </div>
            </div>

            {/* ================= STORE SECTION ================= */}
            <div className="store-section">
              <h2>Stores</h2>

              <div className="controls">
                <input
                  type="text"
                  placeholder=" Search by name or address..."
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                />

                <select value={sort} onChange={(e) => setSort(e.target.value)}>
                  <option value="name:asc">Name A  Z</option>
                  <option value="name:desc">Name Z  A</option>
                  <option value="rating:desc"> Highest Rating</option>
                  <option value="rating:asc"> Lowest Rating</option>
                </select>
              </div>

              {stores.length === 0 ? (
                <p className="empty">No stores found</p>
              ) : (
                <div className="store-grid">
                  {stores.map((store) => (
                    <div key={store.id} className="store-card">
                      <h3>{store.name}</h3>
                      <p><strong>Email:</strong> {store.email}</p>
                      <p><strong>Address:</strong> {store.address}</p>
                      <p>
                         <strong>Avg Rating:</strong>{" "}
                        {store.avgRating
                          ? Number(store.avgRating).toFixed(1)
                          : "N/A"}
                      </p>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </>
        )}
      </div>
    </>
  );
}