import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import API from "../utils/api";
import AdminNavbar from "../components/AdminNavbar";
import "../styles/admin.css";

export default function UserDetail() {
  const { id } = useParams();

  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);

  useEffect(() => {
    fetchUser();
  }, [id]);

  /* ================= FETCH USER ================= */
  const fetchUser = async () => {
    setLoading(true);
    setError(null);

    try {
      const res = await API.get(`/admin/users/${id}`);

      //  SAFE PARSING
      const userData = res.data?.data || res.data;
      setUser(userData);

    } catch (err) {
      console.error(err);
      setError(" Unable to load user details");
    } finally {
      setLoading(false);
    }
  };

  /* ================= VALIDATION ================= */
  const validate = () => {
    if (!user.name || user.name.length < 20 || user.name.length > 60)
      return "Name must be 2060 characters";

    if (!/\S+@\S+\.\S+/.test(user.email))
      return "Invalid email";

    if (user.address && user.address.length > 400)
      return "Address too long";

    return null;
  };

  const validationError = user ? validate() : null;

  /* ================= UPDATE USER ================= */
  const updateUser = async (e) => {
    e.preventDefault();

    const errMsg = validate();
    if (errMsg) {
      setError(errMsg);
      return;
    }

    setError(null);
    setSuccess(null);

    try {
      await API.put(`/admin/users/${id}`, user);
      setSuccess(" User updated successfully");
    } catch (err) {
      console.error(err);
      setError(err.response?.data?.message || " Failed to update");
    }
  };

  /* ================= UI ================= */
  if (loading) return <p className="loading"> Loading user...</p>;
  if (error && !user) return <p className="error-message">{error}</p>;

  return (
    <>
      <AdminNavbar />

      <div className="admin-container">
        <h1 className="admin-title"> User Detail</h1>

        {error && <div className="error-message">{error}</div>}
        {success && <div className="success-message">{success}</div>}

        <form className="form-card" onSubmit={updateUser}>
          {/* NAME */}
          <label>
            Name
            <input
              value={user.name || ""}
              onChange={(e) =>
                setUser({ ...user, name: e.target.value })
              }
            />
          </label>

          {/* EMAIL */}
          <label>
            Email
            <input
              type="email"
              value={user.email || ""}
              onChange={(e) =>
                setUser({ ...user, email: e.target.value })
              }
            />
          </label>

          {/* ROLE */}
          <label>
            Role
            <select
              value={user.role || "user"}
              onChange={(e) =>
                setUser({ ...user, role: e.target.value })
              }
            >
              <option value="user">User</option>
              <option value="admin">Admin</option>
              <option value="store_owner">Store Owner</option>
            </select>
          </label>

          {/* ADDRESS */}
          <label>
            Address
            <input
              value={user.address || ""}
              onChange={(e) =>
                setUser({ ...user, address: e.target.value })
              }
            />
          </label>

          {/*  STORE OWNER INFO */}
          {user.role === "store_owner" && user.Stores && (
            <div className="extra-box">
              <h3> Store Info</h3>

              {user.Stores.length === 0 ? (
                <p>No stores found</p>
              ) : (
                user.Stores.map((store) => {
                  const avg =
                    store.avgRating ||
                    (store.Ratings?.length
                      ? (
                          store.Ratings.reduce(
                            (sum, r) => sum + r.rating,
                            0
                          ) / store.Ratings.length
                        ).toFixed(1)
                      : "N/A");

                  return (
                    <div key={store.id} className="store-mini">
                      <p><strong>{store.name}</strong></p>
                      <p> Avg Rating: {avg}</p>
                    </div>
                  );
                })
              )}
            </div>
          )}

          {/* BUTTON */}
          <button type="submit" disabled={!!validationError}>
            Save Changes
          </button>
        </form>

        <div className="link-box">
          <a href="/change-password"> Change Password</a>
        </div>
      </div>
    </>
  );
}