import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import API from "../utils/api";
import "../styles/user.css";

export default function UserDashboard() {
  const navigate = useNavigate();

  const [stores, setStores] = useState([]);
  const [search, setSearch] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [message, setMessage] = useState(null);
  const [ratingLoading, setRatingLoading] = useState({});

  /* ================= FETCH STORES ================= */
  useEffect(() => {
    const timer = setTimeout(() => {
      fetchStores();
    }, 300);

    return () => clearTimeout(timer);
  }, [search]);

  const fetchStores = async () => {
    setLoading(true);
    setError(null);

    try {
      const res = await API.get(
        `/user/stores?q=${encodeURIComponent(search)}`
      );

      //  FIXED (correct structure)
      setStores(res.data?.data || []);

    } catch (err) {
      console.error("STORE ERROR:", err.response?.data || err.message);
      setError(" Failed to load stores");
    } finally {
      setLoading(false);
    }
  };

  /* ================= RATE STORE ================= */
  const rate = async (id, rating) => {
    if (rating < 1 || rating > 5) return;
    if (ratingLoading[id]) return;

    setError(null);
    setMessage(null);

    setRatingLoading((prev) => ({ ...prev, [id]: true }));

    try {
      await API.post(`/user/rate/${id}`, { rating });

      setMessage(" Rating submitted successfully");

      //  instant UI update
      setStores((prev) =>
        prev.map((s) =>
          s.id === id ? { ...s, myRating: rating } : s
        )
      );

    } catch (err) {
      console.error("RATE ERROR:", err.response?.data || err.message);
      setError(" Unable to submit rating");
    } finally {
      setRatingLoading((prev) => ({ ...prev, [id]: false }));
    }
  };

  /* ================= LOGOUT ================= */
  const logout = () => {
    localStorage.removeItem("token");
    navigate("/login");
  };

  /* ================= UI ================= */
  return (
    <div className="user-container">

      {/* HEADER */}
      <div className="user-header">
        <h1> User Dashboard</h1>

        <div className="actions">
          <button onClick={fetchStores} disabled={loading}>
             Refresh
          </button>

          <button className="logout-btn" onClick={logout}>
             Logout
          </button>
        </div>
      </div>

      {/* STATUS */}
      {error && <div className="error-message">{error}</div>}
      {message && <div className="success-message">{message}</div>}

      {/* SEARCH */}
      <div className="controls">
        <input
          className="search-input"
          placeholder=" Search by name or address"
          value={search}
          onChange={(e) => {
            setSearch(e.target.value);
            setMessage(null);
          }}
        />
      </div>

      {/* STORE LIST */}
      <div className="store-grid">
        {loading ? (
          <p className="loading"> Loading stores...</p>
        ) : stores.length === 0 ? (
          <p className="empty">No stores found</p>
        ) : (
          stores.map((store) => (
            <div key={store.id} className="store-card">
              <h3>{store.name}</h3>
              <p>{store.address}</p>

              <p className="avg">
                 Avg Rating:{" "}
                {store.avgRating
                  ? Number(store.avgRating).toFixed(1)
                  : "N/A"}
              </p>

              <p className="my">
                 My Rating: {store.myRating || "Not Rated"}
              </p>

              {/* STAR RATING */}
              <div className="star-row">
                {[1, 2, 3, 4, 5].map((num) => (
                  <span
                    key={num}
                    className={`star ${
                      store.myRating >= num ? "active" : ""
                    } ${ratingLoading[store.id] ? "disabled" : ""}`}
                    onClick={() => rate(store.id, num)}
                  >
                    
                  </span>
                ))}
              </div>

              {ratingLoading[store.id] && (
                <p className="rating-loading"> Updating...</p>
              )}
            </div>
          ))
        )}
      </div>
    </div>
  );
}