import React, { useEffect } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { jwtDecode } from "jwt-decode";
import "../styles/navbar.css";

export default function AdminNavbar() {
  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem("token");

    if (!token) {
      navigate("/login");
      return;
    }

    try {
      const decoded = jwtDecode(token);

      //  Only admin allowed
      if (decoded.role !== "admin") {
        navigate("/login");
      }
    } catch (err) {
      console.error("Invalid token");
      navigate("/login");
    }
  }, [navigate]);

  const logout = () => {
    localStorage.clear(); //  clears everything (better than removeItem)
    navigate("/login");
  };

  const activeClass = ({ isActive }) => (isActive ? "active" : "");

  return (
    <div className="navbar">
      <h2 className="logo"> Admin Panel</h2>

      <div className="nav-links">
        <NavLink to="/admin" className={activeClass}>
          Dashboard
        </NavLink>

        <NavLink to="/admin/users" className={activeClass}>
          Users
        </NavLink>

        <NavLink to="/admin/create" className={activeClass}>
          Add User/Store
        </NavLink>

        <NavLink to="/change-password" className={activeClass}>
          Security
        </NavLink>
      </div>

      <button className="logout-btn" onClick={logout}>
        Logout
      </button>
    </div>
  );
}