import { useState, useEffect } from "react";
import AdminNavbar from "../components/AdminNavbar";
import API from "../utils/api";
import "../styles/admin.css";

export default function AdminCreate() {
  const [user, setUser] = useState({
    name: "",
    email: "",
    password: "",
    address: "",
    role: "user",
  });

  const [store, setStore] = useState({
    name: "",
    email: "",
    address: "",
    ownerId: "",
  });

  const [owners, setOwners] = useState([]);
  const [message, setMessage] = useState("");
  const [error, setError] = useState("");

  /* ================= FETCH STORE OWNERS ================= */
  useEffect(() => {
    fetchOwners();
  }, []);

  const fetchOwners = async () => {
    try {
      const res = await API.get("/admin/users?role=store_owner");

      const ownerList = Array.isArray(res.data)
        ? res.data
        : res.data.data || [];

      setOwners(ownerList);
    } catch (err) {
      console.error(err);
      setOwners([]);
    }
  };

  /* ================= VALIDATION ================= */
  const validateUser = () => {
    if (user.name.length < 20 || user.name.length > 60)
      return "Name must be between 20 and 60 characters";

    if (!/\S+@\S+\.\S+/.test(user.email))
      return "Invalid email format";

    if (
      user.password.length < 8 ||
      user.password.length > 16 ||
      !/[A-Z]/.test(user.password) ||
      !/[!@#$%^&*]/.test(user.password)
    )
      return "Password must be 8-16 chars, include uppercase & special char";

    if (user.address.length > 400)
      return "Address must be under 400 characters";

    return null;
  };

  const validateStore = () => {
    if (!store.name) return "Store name is required";

    if (!/\S+@\S+\.\S+/.test(store.email))
      return "Invalid store email";

    if (store.address.length > 400)
      return "Store address too long";

    if (!store.ownerId)
      return "Please select a store owner";

    return null;
  };

  /* ================= CREATE USER ================= */
  const createUser = async (e) => {
    e.preventDefault();
    setError("");
    setMessage("");

    const validationError = validateUser();
    if (validationError) {
      setError(validationError);
      return;
    }

    try {
      await API.post("/admin/users", user);

      setMessage(" User created successfully");

      setUser({
        name: "",
        email: "",
        password: "",
        address: "",
        role: "user",
      });

      fetchOwners();
    } catch (err) {
      console.error(err);
      setError(" Failed to create user");
    }
  };

  /* ================= CREATE STORE ================= */
  const createStore = async (e) => {
    e.preventDefault();
    setError("");
    setMessage("");

    const validationError = validateStore();
    if (validationError) {
      setError(validationError);
      return;
    }

    try {
      await API.post("/admin/stores", store);

      setMessage(" Store created successfully");

      setStore({
        name: "",
        email: "",
        address: "",
        ownerId: "",
      });
    } catch (err) {
      console.error(err);
      setError(" Failed to create store");
    }
  };

  return (
    <>
      <AdminNavbar />

      <div className="admin-container">
        <h1>Admin Create Panel</h1>

        {error && <div className="error-message">{error}</div>}
        {message && <div className="success-message">{message}</div>}

        <div className="admin-grid">

          {/* USER FORM */}
          <div className="card">
            <h2>Create User</h2>

            <form onSubmit={createUser}>
              <input
                placeholder="Full Name (2060 chars)"
                value={user.name}
                onChange={(e) =>
                  setUser({ ...user, name: e.target.value })
                }
              />

              <input
                type="email"
                placeholder="Email"
                value={user.email}
                onChange={(e) =>
                  setUser({ ...user, email: e.target.value })
                }
              />

              <input
                type="password"
                placeholder="Password (816, 1 uppercase, 1 special)"
                value={user.password}
                onChange={(e) =>
                  setUser({ ...user, password: e.target.value })
                }
              />

              <input
                placeholder="Address (max 400 chars)"
                value={user.address}
                onChange={(e) =>
                  setUser({ ...user, address: e.target.value })
                }
              />

              <select
                value={user.role}
                onChange={(e) =>
                  setUser({ ...user, role: e.target.value })
                }
              >
                <option value="user">User</option>
                <option value="admin">Admin</option>
                <option value="store_owner">Store Owner</option>
              </select>

              <button type="submit">Create User</button>
            </form>
          </div>

          {/* STORE FORM */}
          <div className="card">
            <h2>Create Store</h2>

            <form onSubmit={createStore}>
              <input
                placeholder="Store Name"
                value={store.name}
                onChange={(e) =>
                  setStore({ ...store, name: e.target.value })
                }
              />

              <input
                type="email"
                placeholder="Store Email"
                value={store.email}
                onChange={(e) =>
                  setStore({ ...store, email: e.target.value })
                }
              />

              <input
                placeholder="Address (max 400 chars)"
                value={store.address}
                onChange={(e) =>
                  setStore({ ...store, address: e.target.value })
                }
              />

              <select
                value={store.ownerId}
                onChange={(e) =>
                  setStore({ ...store, ownerId: e.target.value })
                }
              >
                <option value="">Select Store Owner</option>

                {owners.map((o) => (
                  <option key={o.id} value={o.id}>
                    {o.name} ({o.email})
                  </option>
                ))}
              </select>

              <button type="submit">Create Store</button>
            </form>
          </div>

        </div>
      </div>
    </>
  );
}