import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import API from "../utils/api";
import "../styles/store.css";

export default function StoreDashboard() {
  const navigate = useNavigate();

  const [data, setData] = useState({ ratings: [], avg: 0 });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [sort, setSort] = useState("date:desc");

  useEffect(() => {
    fetchDashboard();
  }, [sort]);

  const fetchDashboard = async () => {
    setLoading(true);
    setError(null);

    try {
      const res = await API.get(`/store/dashboard?sort=${sort}`);

      //  SAFE PARSE
      const safeData = res.data?.data || res.data || {};

      setData({
        ratings: safeData.ratings || [],
        avg: safeData.avg || 0,
      });

    } catch (err) {
      console.error("Error fetching dashboard:", err);
      setError(" Unable to load dashboard");
    } finally {
      setLoading(false);
    }
  };

  /* ================= LOGOUT ================= */
  const logout = () => {
    localStorage.removeItem("token");
    navigate("/login");
  };

  /* ================= STAR RENDER ================= */
  const renderStars = (rating) => (
    <span>
      {[1, 2, 3, 4, 5].map((num) => (
        <span key={num} className={num <= rating ? "star filled" : "star"}>
          
        </span>
      ))}
    </span>
  );

  /* ================= UI ================= */
  if (loading) {
    return <h2 className="loading"> Loading dashboard...</h2>;
  }

  return (
    <div className="store-container">

      {/* HEADER */}
      <div className="store-header">
        <h1 className="store-title"> Store Owner Dashboard</h1>

        <div className="actions">
          <button onClick={fetchDashboard}> Refresh</button>

          <select value={sort} onChange={(e) => setSort(e.target.value)}>
            <option value="date:desc">Newest first</option>
            <option value="date:asc">Oldest first</option>
            <option value="rating:desc">Highest rating</option>
            <option value="rating:asc">Lowest rating</option>
          </select>

          <button className="logout-btn" onClick={logout}>
             Logout
          </button>
        </div>
      </div>

      {/* AVG CARD */}
      <div className="avg-card">
        <h3> Average Rating</h3>
        <p className="avg-number">
          {data.avg ? Number(data.avg).toFixed(1) : "N/A"}
        </p>
      </div>

      {/* ERROR */}
      {error && <div className="error-message">{error}</div>}

      {/* RATINGS TABLE */}
      <div className="ratings-section">
        <h2> Users Who Rated Your Store</h2>

        {!data.ratings || data.ratings.length === 0 ? (
          <p className="empty">No ratings yet</p>
        ) : (
          <table className="ratings-table">
            <thead>
              <tr>
                <th>User Name</th>
                <th>Email</th>
                <th>Rating</th>
              </tr>
            </thead>

            <tbody>
              {data.ratings.map((r, index) => (
                <tr key={index}>
                  <td>{r.User?.name || "Unknown"}</td>
                  <td>{r.User?.email || "N/A"}</td>
                  <td>{renderStars(r.rating)}</td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}